html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: sans-serif;
  color: #3c3c3c;
  background-color: #3c3c3c;
  background-repeat: no-repeat;
  background-position: top right;
  background-size: auto 1080px;
}
body.webp-true {
  background-image: url("../img/hero3.webp");
}
body.webp-false {
  background-image: url("../img/hero3.jpg");
}
@media (min-width: 767px) and (min-height: 767px) {
  body {
    background-size: auto 1280px;
  }
}

.container {
  max-width: 1400px;
  padding-top: 0;
  padding-right: 20px;
  padding-bottom: 0;
  padding-left: 20px;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 0;
  margin-left: auto;
}

.section {
  padding-top: 88px;
}

.title {
  text-align: center;
  position: relative;
  font-size: 1.2em;
  line-height: 6em;
  font-weight: 700;
}
.title::before {
  position: absolute;
  top: 20px;
  right: auto;
  left: 50%;
  bottom: auto;
  transform: none;
  transform: translateX(-50%);
  font-size: 4em;
  line-height: 1em;
  font-weight: 700;
}
@media (min-width: 767px) {
  .title {
    font-size: 2em;
    line-height: 5em;
    font-weight: 700;
  }
}

.subtitle {
  font-size: 1.1em;
  line-height: 4em;
  font-weight: 700;
}
@media (min-width: 1024px) {
  .subtitle {
    font-size: 1.4em;
    line-height: 4em;
    font-weight: 700;
  }
}

.section-subtitle {
  text-align: center;
  padding-top: 0;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 0;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 20px;
  margin-left: 0;
  font-size: 0.8em;
  line-height: 2em;
  font-weight: 700;
}
@media (min-width: 767px) {
  .section-subtitle {
    font-size: 1.4em;
    line-height: 4em;
    font-weight: 700;
