    font-weight: 400;
  }
}

.date {
  font-size: 0.8em;
  line-height: 4em;
  font-weight: 700;
}

a:focus,
input:focus,
textarea:focus {
  outline: none;
  box-shadow: 0 0 0 3px #0e71af;
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 3px #eae4f5;
}

a,
button {
  transition: all 0.44s linear;
}

a:hover {
  opacity: 0.8;
}

button:hover {
  background-color: #3c3c3c;
}

.button {
  color: #eae4f5;
  background-color: #0e71af;
  border: none;
  width: 200px;
  height: auto;
}
.button:disabled {
  opacity: 0.5;
}

.header {
  border-bottom: 2px solid rgba(234, 228, 245, 0.5);
}
.header-fixed {
  animation-duration: 1s;
  animation-name: header-animation-fixed;
  z-index: 99;
  background-color: #3c3c3c;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: auto;
  transform: none;
}
.header-static {
  animation-duration: 1s;
  animation-name: header-animation;
}
.header-inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  row-gap: normal;
  column-gap: normal;
  padding-top: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
}
.header-logo {
  background-color: #0e71af;
  color: #eae4f5;
  box-shadow: 6px 6px 0 #eae4f5;
  padding-top: 16px;
  padding-right: 16px;
  padding-bottom: 16px;
  padding-left: 16px;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
}
.header-menu {
  background: transparent;
  border: none;
  font-size: 0;
  border-top: 2px solid #0e71af;
  border-bottom: 2px solid #0e71af;
  position: relative;
  width: 30px;
  height: 2px;
  padding-top: 12px;
  padding-right: 0;
  padding-bottom: 12px;
  padding-left: 0;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
}
.header-menu::before {
  content: "";
  position: absolute;
  left: 2px;
  top: 12px;
  background-color: #eae4f5;
  position: absolute;
  top: 12px;
  right: auto;
  left: 2px;
  bottom: auto;
  transform: none;
  width: 26px;
  height: 1px;
}
@media (min-width: 767px) {
  .header-menu {
    display: none;
  }
}
.header-navigation {
  background-color: rgba(234, 228, 245, 0.5);
  opacity: 0;
  transition: all 0.44s linear;
  z-index: -99;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: -1000px;
  right: 0;
  left: 0;
  bottom: 0;
  transform: none;
}
@media (min-width: 767px) {
  .header-navigation {
    position: static;
    background: transparent;
    z-index: auto;
    opacity: 1;
    width: auto;
    height: auto;
  }
}
.header-navigation--show {
  opacity: 1;
  z-index: 99;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  transform: none;
}
.header-links {
  z-index: 100;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #eae4f5;
  width: 280px;
  height: auto;
  padding-top: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 0;
  margin-left: auto;
}
@media (min-width: 767px) {
  .header-links {
    background-color: transparent;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: stretch;
    flex-wrap: nowrap;
    row-gap: 15px;
    column-gap: 15px;
    width: auto;
    height: auto;
  }
}
.header .links-item {
  padding-top: 0;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 0;
  margin-top: 5px;
  margin-right: 0;
  margin-bottom: 5px;
  margin-left: 0;
}
.header-link {
  color: #3c3c3c;
  transition: all 0.44s linear;
}
.header-link--active {
  opacity: 0.6;
  pointer-events: none;
  text-decoration: underline;
}
@media (min-width: 767px) {
  .header-link {
    color: #eae4f5;
  }
}

.navigation-close {
  background: transparent;
  border: none;
  color: #eae4f5;
  border-radius: 50%;
  background-color: #3c3c3c;
  width: 58px;
  height: 58px;
  padding-top: 5px;
  padding-right: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
  position: fixed;
  top: auto;
  right: auto;
  left: 50%;
  bottom: 20px;
  transform: none;
  transform: translateX(-50%);
  font-size: 2em;
  line-height: 1em;
  font-weight: 700;
}
@media (min-width: 767px) {
  .navigation-close {
    display: none;
  }
}

@keyframes header-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes header-animation-fixed {
  0% {
    top: -108px;
    opacity: 0;
  }
  100% {
    top: 0;
    opacity: 1;
  }
}
.hero {
  text-align: center;
  width: auto;
  height: calc(100vh - 90px);
}
