.hero-scroll {
  color: white;
  width: 200px;
  height: auto;
  font-size: 0.8em;
  line-height: 4em;
  font-weight: 700;
  position: absolute;
  top: auto;
  right: auto;
  left: 50%;
  bottom: 50px;
  transform: none;
  transform: translateX(-50%);
}
.hero-scroll div {
  border: 1px solid white;
  border-radius: 50%;
  transform: rotate(90deg);
  padding-top: 0;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 0;
