.alert {
  z-index: 100;
  text-align: center;
  transition: all 0.44s linear;
  padding-top: 15px;
  padding-right: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
  width: 300px;
  height: auto;
  position: fixed;
  top: -3000px;
  right: auto;
  left: 50%;
  bottom: auto;
  transform: none;
  transform: translateX(-50%);
}
.alert-text {
  color: #eae4f5;
  font-size: 1em;
  line-height: 1.6em;
  font-weight: 700;
}
.alert-exit {
  color: #eae4f5;
  border: none;
  background: none;
  font-size: 1em;
  line-height: 1em;
  font-weight: 700;
  position: absolute;
  top: 10px;
  right: 10px;
  left: auto;
  bottom: auto;
  transform: none;
}
.alert-show {
  position: fixed;
  top: 0;
  right: auto;
  left: 50%;
  bottom: auto;
  transform: none;
  transform: translateX(-50%);
}
.alert--success {
  background-color: green;
}
.alert--error {
  background-color: crimson;
}
@media (min-width: 767px) {
  .alert {
    position: fixed;
    top: -3000px;
    right: 30px;
    left: auto;
    bottom: auto;
    transform: none;
  }
  .alert-show {
    position: fixed;
    top: 30px;
    right: 30px;
    left: auto;
    bottom: auto;
    transform: none;
  }
}